<template>
  <div class="container">
    <a-row :gutter="[24]">
      <a-col
        :xs="{ span: 24, offset: 0 }"
        :md="{ span: 12, offset: 6 }"
        :lg="{ span: 12, offset: 6 }"
        :xl="{ span: 8, offset: 8 }"
        class="center"
      >
        <div class="container-title">
          <span class="subtitle">Вход в личный кабинет</span>
          <h2 class="page-title">Введите e-mail и пароль</h2>
        </div>
        <x-form @submit="onSubmit" v-model="formData" :values="formData">
          <x-input
