            Войти
          </a-button>
          <router-link to="/application" style="display: block; text-align: center; margin-top: 20px">
            <span>Еще нет аккаунта? Зарегистрируйтесь</span>
          </router-link>
        </x-form>
      </a-col>
    </a-row>
  </div>
