<script>
export default {
  data() {
    return {
      formData: {
        email: 'deniskindo@mail.ru',
        password: '123456',
      },
    }
  },
  methods: {
    async onSubmit(form) {
      console.log(form)
      try {
        await this.$store.dispatch('user/login', form)
      } catch (error) {
        console.log(error.response)
        this.$errors.showOnce('Неправильный логин или пароль')
      }
    },
  },
}
</script>

